import React, { useState } from 'react';
import { v4 as uuid } from 'uuid';
import { useDispatch } from 'react-redux';

import { Button, ButtonGroup } from '@components/shared/Button/Button';

import { addCard } from '@redux/cards';

import style from './AddCardForm.module.scss';

export interface IAddCardFormProps {
  column: string;
}

export const AddCardForm: React.FunctionComponent<IAddCardFormProps> = ({
  column
}) => {
  const [isOpen, setIsOpen] = useState(false);
  const [title, setTitle] = useState<string>('');
  const dispatch = useDispatch();

  const handleAdd = function () {
    close();
    dispatch(addCard({ title, text: '', id: uuid(), column }));
  };

  const close = function () {
    setIsOpen(false);
    setTitle('');
  };

  return <div>
    {isOpen ?
      <div>
        <textarea
          value={title}
          onChange={(e) => setTitle(e.target.value)}
          className={style.AddFromTextarea}
          placeholder="Ð’Ð²ÐµÐ´Ð¸Ñ‚Ðµ Ñ‚ÐµÐºÑÑ‚ ÐºÐ°Ñ€Ñ‚Ð¾Ñ‡ÐºÐ¸"
          autoFocus
        >
        </textarea>
        <div>
          <ButtonGroup>
            <Button
              type="primary"
              onClick={() => handleAdd()}
              disabled={!title}
            >
              Ð”Ð¾Ð±Ð°Ð²Ð¸Ñ‚ÑŒ ÐºÐ°Ñ€Ñ‚Ð¾Ñ‡ÐºÑƒ
            </Button>
            <Button
              type="secondary"
              onClick={() => close()}
              icon="close"
            >
              ÐžÑ‚Ð¼ÐµÐ½Ð°
            </Button>
          </ButtonGroup>
        </div>
      </div>:
      <Button
        type="secondary"
        onClick={() => setIsOpen(true)}
        icon="add"
      >
        Ð”Ð¾Ð±Ð°Ð²Ð¸Ñ‚ÑŒ ÐµÑ‰Ðµ Ð¾Ð´Ð½Ñƒ ÐºÐ°Ñ€Ñ‚Ð¾Ñ‡ÐºÑƒ
      </Button>
    }
    
  </div>;
};